/**
 *
 * @param {Object} err
 * @returns {String}
 */
export const handleError = (err) => {
  if (err.response) {
    // console.log(err.response.data);
    if (err.response.data.error) {
      return err.response.data.error;
    } else {
      return 'Something went wrong!';
    }
  } else {
    return err.message;
  }
};
